.form-div {
    grid-area: form;
    text-align: center;
    width: 100%;
    margin-bottom: 5rem;
}

    .form-elements {
        border: 1px solid rgba(206, 151, 151, 0.5);
        border-right: none;
        margin: 0 auto;
        width: 80%;
        border-radius: 3rem;
        height: 3rem;
        display: -ms-flexbox;
        display: flex;
        -ms-flex-pack: justify;
            justify-content: space-between;
        -ms-flex-align: center;
            align-items: center;
    }

        .input-control {
            color: hsl(0, 40%, 76%);  
            font-family: 'Josefin Sans', sans-serif;
            position: relative;
            display: -ms-flexbox;
            display: flex;
            -ms-flex-direction: row;
                flex-direction: row;
            border: none;
            width: 85%;
            height: 85%;
