            margin-left: 1.5rem;
            outline: none;
        }

        .input-control-err {
            border: 1px solid hsl(0, 100%, 50%);
            border-right: none;
        }

        .input-control::-webkit-input-placeholder {
            color: hsl(0, 40%, 76%); 
        }

        .input-control::-moz-placeholder {
            color: hsl(0, 40%, 76%); 
        }

        .input-control::-ms-input-placeholder {
            color: hsl(0, 40%, 76%); 
        }

        .input-control::placeholder {
            color: hsl(0, 40%, 76%); 
        }

        .btn-error {
            margin-right: 10px;
            visibility: hidden;
        }

        .button-control {
            font-family: 'Josefin Sans', sans-serif;
            border-radius: 30px;
            color:hsl(0, 98%, 98%);
            border: 1px solid rgba(206, 151, 151, 0.5);
            margin-right: -5px;
            width: 5rem;
            height: 3.2rem;
            font-size: 2rem;
            background: -webkit-linear-gradient(
                315deg,
                rgb(248, 191, 191) 30%,
                rgb(240, 139, 139) 100%
            );
            background: -o-linear-gradient(
                315deg,
                rgb(248, 191, 191) 30%,
                rgb(240, 139, 139) 100%
            );
            background: linear-gradient(
                135deg,
                rgb(248, 191, 191) 30%,
                rgb(240, 139, 139) 100%
            );

            -webkit-box-shadow: 0px 10px 30px 0px rgba(222,135,135,0.72);

                    box-shadow: 0px 10px 30px 0px rgba(222,135,135,0.72);
        }

        .button-control:hover {
            -webkit-transform: scale(1.03);
                -ms-transform: scale(1.03);
                    transform: scale(1.03);
