                    box-shadow: 0px 5px 15px 0px rgba(222,135,135,0.72);
        }

        .button-control:active {
            -webkit-transform: scale(0.99);
                -ms-transform: scale(0.99);
                    transform: scale(0.99);
        }

        .mail-error {
            font-family: 'Josefin Sans', sans-serif;
            font-size: 0.8rem;
            font-weight: 800;
            margin-top: 1rem;
            line-height: 1.3rem;

            color:rgb(255, 0, 0);
            visibility: hidden;
        }

        .mail-error-visible {
            visibility: visible;
        }





